/**
 * 
 * @param  {any} input    
 * @return {string}       'array', 'object', 'function', 'null', 'undefined', 'string', 'number','boolean'
 */
export default function (input) {
  return ({}).toString.call(input).slice(8, -1).toLowerCase()
}



