/**
 * 
 * @param fun Function
 * @param context Object 
 * @param delay Nubmer 
 * @returns {Function}
 */
export function debounce(fun,context,delay=200){
  if(typeof fun!=="function"){
    console.warn('')
    return fun
  }
  let time=null;
  return function () {
    let arg=[].call(arguments,0)
    clearTimeout(time);
    time=setTimeout(()=>{
      fun.apply(context,arg)
    },delay)
  }
}

/**
 * 
 * @param fun Function
 * @param context Object 
 * @param interval Nubmer 
 * @returns {Function}
 */
export function throttle(fun,context,interval=30) {
  if(typeof fun!=="function"){
    console.warn('')
    return fun
  }
  let startTime=0;
  let endTime;
  return function () {
    let arg=[].slice.call(arguments,0);
    endTime=new Date().getTime();
    if(endTime-startTime>interval){
      fun.apply(context,arg)
      startTime=endTime
    }
  }
}

/***
 * 
 * @param fun Function
 * @param context Object 
 * @returns {Function}
 */
export function curring(fun,context) {
  if(typeof fun!=="function"){
    console.warn('')
    return fun
  }
  let arg=[].slice.call(arguments,1);
  return function() {
    let newArgs = arg.concat([].slice.call(arguments));
    return fun.apply(context, newArgs);
  };
}



