const URL = {
  // 
  href: window.location.href,
  // 
  search: window.location.search,
  // URL
  query: function (key) {
    let reg = new RegExp('(^|&|\\?|#)' + key + '=([^&]*)(&|\x24)', '')
    let match = this.search.substr(1).match(reg)
    if (match) {
      return match[2]
    }
    return ''
  }
}
export default URL



